___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: "Orbitron", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  height: 100%;
  max-height: 100dvh;
  overflow-x: hidden;
}
header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 75px;
  max-height: 15dvh;
}
.game_area {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  height: 80dvh;
  width: 100%;
}
.box_area {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 25px;
  padding: 20px;
}
.placeShip_Computer_area {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 25px;
  padding: 20px;
}
.board {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  gap: 0;
  width: calc(85% - 20px);
  height: calc(85% - 20px);
}
.box_area .board .cell_common {
  /* height: 20px;
  max-height: 35px;
  width: 20px;
  max-width: 35px; */
  /* border: 1px solid black; */
}
.cell_common {
  /* width: 30px;
  height: 30px; */
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #ccc;
  position: relative;
}

.cell_miss {
  background-color: #f0f0f0;
}

.cell_miss::before {
  content: "";
  width: 10px;
  height: 10px;
  background-color: black;
  border-radius: 50%;
  position: absolute;
}

.cell_hit {
  background-color: #f0f0f0;
  pointer-events: none;
  /* Add Cross when hover over here */
}

.cell_hit::before,
.cell_hit::after {
  content: "";
  width: 20px;
  height: 2px;
  background-color: red;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}

.cell_hit::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.ship_area {
  background-color: rgb(62, 62, 62);
}
.gameStatus {
  display: flex;
  justify-content: center;
  height: 5dvh;
  align-items: flex-start;
}
.carrier {
  background-color: rgb(239, 239, 72);
}
.battleship {
  background-color: aquamarine;
}
.destroyer {
  background-color: brown;
}
.submarine {
  background-color: coral;
}
.patrol {
  background-color: darkcyan;
}
.placeShip_Computer_area {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.axis_select {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.axis_select button {
  background-color: #007bff; /* Primary blue color */
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 0 10px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.axis_select button:hover {
  background-color: #0056b3; /* Darker blue for hover state */
}

.axis_select button:active {
  background-color: #003f7f; /* Even darker blue for active state */
}

.axis_select .x_axis {
  background-color: #28a745; /* Green for X axis button */
}

.axis_select .x_axis:hover {
  background-color: #218838; /* Darker green for hover state */
}

.axis_select .x_axis:active {
  background-color: #1e7e34; /* Even darker green for active state */
}

.axis_select .y_axis {
  background-color: #dc3545; /* Red for Y axis button */
}

.axis_select .y_axis:hover {
  background-color: #c82333; /* Darker red for hover state */
}

.axis_select .y_axis:active {
  background-color: #bd2130; /* Even darker red for active state */
}

/* .place_ship_text {
  text-align: center;
  margin-top: 20px;
} */
.ship_Pos_Err {
  background-color: #d95b68;
}

.place_ship_text h1 {
  font-size: 24px;
  color: #333; /* Dark grey for the heading */
}

.place_ship_text h1 b {
  color: #007bff; /* Primary blue for the highlighted ship name */
  text-transform: capitalize;
}

@media only screen and (max-width: 600px) {
  .game_area {
    grid-template-columns: repeat(1, 1fr);
  }
  .board {
    width: 100%;
    height: 100%;
  }
}
